  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #FFF;
  font-family: "SF Pro Display", "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--theme1000);
  color: var(--theme000);
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 10px;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.container {
  width: 100%;
  padding: 0 12px;
  margin: 0 auto;
}

.wrapper {
  width: 100%;
  max-width: 360px;
  margin: 0 auto;
}

.btn {
  min-height: 44px;
  padding: 15px;
  font-weight: 800;
  font-size: 14px;
  line-height: 100%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #fff;
  background: var(--accent);
  border-radius: 30px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.btn svg {
  width: 24px;
  height: 24px;
}
.btn-double span {
  margin: 0 10px;
}

.btn-circle {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: var(--theme850);
}
.btn-circle svg {
  width: 24px;
  height: 24px;
}

.title {
  font-weight: 800;
  font-size: 16px;
  line-height: 120%;
  text-transform: uppercase;
}

.header {
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid var(--theme800);
}
.header__top {
  padding: 10px 0;
  background: var(--theme850);
}
.header__lang {
  font-weight: 600;
  font-size: 12px;
  line-height: 100%;
  color: var(--theme200);
  font-weight: 600;
  font-size: 12px;
  line-height: 100%;
}
.header__menu {
  padding: 20px 0;
  display: flex;
  align-items: center;
}
.header__burger {
  margin-right: 15px;
  background: var(--accent);
  box-shadow: 0px 5px 10px rgba(232, 25, 67, 0.35);
}
.header__logo {
  margin-right: auto;
}
.header__manage {
  display: flex;
  align-items: center;
}
.header__manage-item {
  margin-left: 6px;
}
.header__user {
  margin-left: 6px;
}
.header__user .btn-circle {
  background: var(--accent);
}
.header__music {
  display: flex;
  align-items: center;
  overflow-x: scroll;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}
.header__music::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.header__music-item {
  margin-right: 24px;
}
.header__music-link {
  display: flex;
  align-items: center;
  white-space: nowrap;
  text-transform: uppercase;
  font-weight: 700;
  color: var(--theme200);
  font-size: 12px;
  line-height: 100%;
}
.header__music-icon {
  width: 20px;
  min-width: 20px;
  height: 20px;
  margin-right: 8px;
  fill: currentColor;
}

.main {
  flex-grow: 1;
}

.listen {
  margin-bottom: 20px;
  color: #fff;
  text-align: center;
}
.listen__inner {
  padding: 0;
  position: relative;
}
.listen__bg {
  left: 12px;
  right: 12px;
}
.listen__info {
  min-height: 242px;
  padding: 20px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 5;
  overflow: hidden;
}
.listen__title {
  margin-bottom: 10px;
  font-weight: 900;
  font-size: 27px;
  line-height: 110%;
  letter-spacing: 0.02em;
}
.listen__text {
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 13px;
  line-height: 130%;
  letter-spacing: 0.02em;
}
.listen__form {
  width: 100%;
}
.listen__form-label {
  border-radius: 10px;
  position: relative;
}
.listen__form-field {
  width: 100%;
  padding: 15px 50px 15px 20px;
  background: #fff;
  box-shadow: 0px 5px 10px rgba(57, 62, 105, 0.4);
  border-radius: 10px;
  border: none;
  color: var(--theme350);
}
.listen__form-field::placeholder {
  color: var(--theme350);
  font-weight: 300;
  font-size: 15px;
  line-height: 100%;
}
.listen__form-btn {
  width: 24px;
  height: 24px;
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
}

.features {
  margin-bottom: 24px;
}
.features__inner {
  padding: 0;
